<template>
  <div class="personal-content-body">
    <div class="table-content">
      <div class="clear table-content-title">
        <div class="left">公告发布</div>
        <el-input placeholder="搜索" v-model="searchText" class="header-right-time right" :clearable='true' @change="initData()">
          <i slot="prefix" class="el-input__icon el-icon-search "></i>
        </el-input>
        <!-- <el-date-picker v-model="selectTime" type="date" placeholder="选择日期" :clearable='true' value-format="yyyy-MM-dd" @change="getListData(1)" class="header-right-time right">
                </el-date-picker> -->
      </div>
      <div class="table-group">
        <div class="table-group-content">
          <div class="clear table-group-title">
            <div class="left breadcrumb">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/home/investProjectManageHomepage' ,query: {id:projectId}}" v-if="type==2">返回项目首页</el-breadcrumb-item>
                <el-breadcrumb-item>公告发布（{{total}}）</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
            <div class="right">
              <div class="table-group-title-btn btn-blue" @click="goCreatnew">
                <span></span>发布新公告
              </div>
            </div>
            <!-- <div class="line"></div> -->
          </div>
          <div class="table-group-tab">
            <!-- <div class="table-group-tab-item">个人信息</div>
                    <div class="table-group-tab-item current">密码设置</div> -->
            <div class="clear"></div>
            <div class="line"></div>
          </div>
          <div class="historyapply-group-tablebox table-group-tablebox">
            <div class="table-normal-box">
              <table class="table-normal">
                <tr>
                  <!-- <th>
                            <el-checkbox v-model="selectAll" @change="selectallclick($event)"></el-checkbox>
                          </th> -->
                  <th width="30">序号</th>
                  <th>文章标题</th>
                  <th>目标公司</th>
                  <th>发送时间</th>
                  <th width="90">操作</th>
                </tr>
                <tr v-for="(item,index) in datalist" :key="index">
                  <!-- <td>
                            <el-checkbox :disabled="item.user_id!=null" v-model="item.checked" @change="selectoneclick($event)"></el-checkbox>
                          </td> -->
                  <td>{{pageSize*(currentPage-1)+index+1}}</td>
                  <td>{{item.title}}</td>
                  <td v-if="item.nameList.length==totalCompanyList.length">
                    <el-popover placement="top-start" width="230" height='500' trigger="click">
                      <div v-for="item1 of item.nameList">{{item1}}</div>
                      <span slot="reference" class="cursor">全部公司</span>
                    </el-popover>
                  </td>
                  <td v-if="item.nameList.length!=totalCompanyList.length&&item.nameList.length>1">
                    <el-popover placement="top-start" width="230" height='500' trigger="click" :content="item.name">
                      <div v-for="item1 of item.nameList">{{item1}}</div>
                      <span slot="reference" class="cursor">多个公司</span>
                    </el-popover>
                  </td>
                  <td v-if="item.nameList.length==1">{{item.name}}</td>
                  <td>{{item.issue_time}}</td>
                  <td>
                    <span class="table-link" @click="goEditorNews(item.id,item.aif)">编辑</span>
                    <span class="table-link" @click="deleteNews(item.id,item.aif)">删除</span>
                  </td>
                </tr>
              </table>
              <div class="table-nodata" v-if="total==0">
                <img src="../../images/table_nodata.png"> 没有对应数据
              </div>
            </div>
            <div class="pagination-normal" v-if="total!=0">
              <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  afficheList,
  delAffiche,
  bigeventCompanyList,
} from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      projectId: '',
      type: 1, //1=>1级路由，2=>2级路由
      currentPage: 1,
      total: null,
      pageSize: 10,
      searchText: '', //查询数据
      datalist: [], //项目列表,
      totalCompanyList: [],
      companyNameList: null,
    }
  },
  created() {
    this.init();
  },
  watch: {
    $route(to, from) {
      this.init();
    }
  },
  computed: {},
  mounted() {},
  methods: {
    init() {
      if (this.$route.query.projectId != undefined) {
        this.projectId = this.$route.query.projectId;
        this.type = 2;
      } else {
        this.type = 1;
        this.projectId = ''
      }
      this.initData();
      this.getCompanyList();
    },
    initData() {
      this.datalist = [];
      let param = {
        number: this.pageSize,
        page: this.currentPage,
        search: this.searchText
      }
      if (this.projectId != '') {
        param.projectId = this.projectId
      }
      afficheList(param).then(res => {
        if (res.data.msgNo == 1) {
          // console.log(res.data.payload);
          this.datalist = res.data.payload.data;
          // console.log(this.datalist);
          this.datalist.forEach(item => {
            item.nameList = item.name.split(',');
            // let len = item.nameList.length;
            // for (var i = 0; i < len; i++) {
            //     for (var j = 0; j < len - 1 - i; j++) {
            //         if (item.nameList[j].length > item.nameList[j+1].length) {        // 相邻元素两两对比
            //             var temp = item.nameList[j+1];        // 元素交换
            //             item.nameList[j+1] = item.nameList[j];
            //             item.nameList[j] = temp;
            //         }
            //     }
            // }
          });
          this.total = res.data.payload.total;
        }
      })
    },
    currentChange(val) {
      if (this.total > this.pageSize) {
        this.currentPage = val;
        this.initData();
      }
    },
    getCompanyList() {
      bigeventCompanyList().then(res => {
        if (res.data.msgNo == 1) {
          // console.log(res.data.payload);
          let data = res.data.payload
          this.totalCompanyList = data;
        }
      })
    },
    //删除本条数据
    deleteNews(id, aif) {
      dialogConfirmHasbtn('确定要删除本条新闻？').then(() => {
        this.addaccountDialogVisible = false;
        delAffiche({
          id: id,
          aif: aif,
        }).then(res => {
          //console.log(res);
          if (res.data.msgNo == 1) {
            //console.log(res.data.payload);
            dialogConfirm('success', '删除成功')
            this.initData();
          }
        })
      }).catch(() => {});
    },
    //编辑本条数据
    goEditorNews(id, aif) {
      this.$router.push({
        path: '/home/investedNewsAdd',
        query: {
          aif: aif,
        }
      })
    },
    //去新建新闻
    goCreatnew() {
      this.$router.push({
        path: '/home/investedNewsAdd',
      })
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
.cursor {
  cursor: pointer;
  color: #3C98E8;
}

.table-group-title-btn {
  line-height: 30px;
  font-size: 14px;
  padding: 0px 10px;
  margin-right: 32px;
  &>span {
    display: inline-block;
    width: 20px;
    height: 20px;
    float: left;
    margin-top: 5px;
    margin-right: 5px;
    background: url('../../images/projectpermissions_icon.png') no-repeat
  }
}

.peopleSelectTitle-text {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 40px;
  margin-right: 10px;
}

.peopleSelectTitle {
  border-bottom: 1px solid #dcdfe6;
}

el-popover {
  height: 500px;
}

</style>



